.footer-container {
  background-color: #000000;
  height: fit-content;
  .features-strip-container {
    height: rem-calc(57px);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: #f1f1f1; }

  .vertical-divider {
    background-color: #c8c8c8;
    height: rem-calc(30px);
    width: rem-calc(1px); }

  .page-links-container {
    display: flex;
    justify-content: space-between;
    padding: rem-calc(60px) rem-calc(72px) 0 rem-calc(71px);
    font-size: rem-calc(12px);
    color: #ffffff;

    .header {
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: 1px; }
    .content {
      display: flex;
      flex-direction: column;
      height: rem-calc(100px);
      justify-content: space-evenly; } }

  .copyright {
    margin: 1rem 0 rem-calc(13px);
    opacity: 0.5;
    font-family: OpenSans;
    font-size: 12px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff; } }
